@import "https://fonts.googleapis.com/css?family=Roboto+Slab";
html, body {
  width: 100%;
  height: 100%;
  background: #141414;
  margin: 0;
  padding: 0;
}

#clock {
  width: 930px;
  height: 200px;
  text-align: center;
  margin: auto auto auto -465px;
  position: absolute;
  inset: 0 0 0 50%;
}

.num {
  width: 140px;
  height: 200px;
  color: #404040;
  box-sizing: border-box;
  background: #5a5a5a;
  border: 3px solid #404040;
  border-radius: 18px;
  font: 140px Roboto Slab, sans-serif;
  display: inline-block;
  position: relative;
}

.colon {
  color: #404040;
  font: 140px Roboto Slab, sans-serif;
  display: inline-block;
  position: relative;
  top: -60px;
  left: -2px;
}

.base span {
  width: 100%;
  display: block;
  position: absolute;
}

.upper {
  width: 100%;
  height: 50%;
  box-sizing: border-box;
  background: linear-gradient(0deg, #c8c8c8, #fff 15%);
  border-bottom: 1px solid #00000080;
  border-radius: 15px 15px 0 0;
  position: absolute;
  overflow: hidden;
  box-shadow: inset 0 1px 8px #0000001a;
}

.lower {
  width: 100%;
  height: 50%;
  box-sizing: border-box;
  background: linear-gradient(#c8c8c8, #fff 15%);
  border-top: 1px solid #ddd;
  border-radius: 0 0 15px 15px;
  position: absolute;
  top: 50%;
  overflow: hidden;
  box-shadow: inset 0 -3px 5px #0003;
}

.lower span {
  position: relative;
  top: -100%;
}

